.mobile-menu-open {

  margin: 0;
  width: 32px;
  height: 22px;
  padding: 0;
  line-height: 0;
border: none;
background-color: transparent;
}
.mobile-menu-close-icon {
    width: 8px;
        height: 8px;
        fill: currentColor;
        transition: color var(--transition-duration-func);
}
.mobile-menu-open-icon {
 width: 32px;
 height: 22px;
fill: currentColor;
    transition: color var(--transition-duration-func);
}
.mobile-menu-open .icon-cross {
    display: none;
}
.mobile-menu-open.is-open .icon-cross {
    display: block;
}
 @media screen and (min-width: 768px) {
    
     .mobile-menu-open {
        display: none;
     }
 }
