.switcher{
    display: inline-block;
    position: relative;
    width: 40px;
    height: 20px;

   /* background-color: #ffffff;*/
   
}
.switcher-input{
height: 0;
width: 0;
opacity: 0;

}
.switcher-slider{
position: absolute;
cursor: pointer;
border-radius: 30px;
left: 0;
bottom: 8.33%;
top: 8.33%;
right: 0;
background:  linear-gradient(180deg, #4F2EE8 0%, #DCDCDC 100%);
}

.switcher-slider::before{
  content: "";
position: absolute;
cursor: pointer;
border-radius: 50%;
left: 2.5%;
top:1px;
bottom: 12.5%;
right: 52.5%;

height: 15px;
width: 15px;
transition:  transform .25s cubic-bezier(.4,0,.2,1);
background: linear-gradient(180deg, #FFFFFF 0%, #F7F7F7 100%);
box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), 0px 2px 2px rgba(0, 0, 0, 0.1), 1px 2px 4px rgba(0, 0, 0, 0.1);
}
.switcher-input:focus +.switcher-slider {
box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.1);
}
.switcher-input:checked +.switcher-slider{
background:  linear-gradient(180deg, #4F2EE8 0%, #DCDCDC 100%);

}
.switcher-input:checked +.switcher-slider::before{
  transform: translateX(23px);
}











