.switcher {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switcher-input {
  height: 0;
  width: 0;
  opacity: 0;
}

.switcher-slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 30px;
  position: absolute;
  top: 8.33%;
  bottom: 8.33%;
  left: 0;
  right: 0;
}

.switcher-slider:before {
  content: "";
  cursor: pointer;
  height: 15px;
  width: 15px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 1px;
  bottom: 12.5%;
  left: 2.5%;
  right: 52.5%;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.switcher-input:focus + .switcher-slider {
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switcher-input:checked + .switcher-slider {
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
}

.switcher-input:checked + .switcher-slider:before {
  transform: translateX(23px);
}

